
//////////////////////
import { Component } from '@angular/core';
import { FormBuilder, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import { AuthService } from '../services/auth.service';
import { Router } from '@angular/router';
import { CommonModule } from '@angular/common';
import { MatCardModule } from '@angular/material/card';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatButtonModule } from '@angular/material/button';
import { MatInputModule } from '@angular/material/input';
import { MatIconModule } from '@angular/material/icon';
import { MatCheckboxModule } from '@angular/material/checkbox';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  standalone: true,
  imports: [
    CommonModule,
    ReactiveFormsModule,
    MatCardModule,
    MatFormFieldModule,
    MatInputModule,
    MatButtonModule,
    MatIconModule,
    MatCheckboxModule
  ]
})
export class LoginComponent {
  loginForm: FormGroup;
  loading = false;
  errorMessage: string = '';
  hidePassword = true;
  constructor(
    private fb: FormBuilder,
    private authService: AuthService,
    private router: Router
  ) {
    console.log('üü¢ LoginComponent cargado correctamente');
    this.loginForm = this.fb.group({
      username: ['', Validators.required],
      password: ['', Validators.required],
      rememberMe: [false]

    });
  }

  onSubmit() {
    if (this.loginForm.invalid) return;

    this.loading = true;
    const loginRequest = {
      usuarioUsu: this.loginForm.value.username, // üëà Mapeo username a usuarioUsu
      passwordUsu: this.loginForm.value.password // üëà Mapeo password a passwordUsu
    };

    console.log('Credenciales enviadas:', loginRequest); // log para verificar las credenciales


    this.authService.login(loginRequest).subscribe({
      next: (res) => {
        console.log('‚úÖ Login correcto:', res);

        if (res.token) {
          localStorage.setItem('token', res.token);
          //this.router.navigate(['/admin']);
        } else {
          this.errorMessage = 'Token no recibido del servidor';
        }

        this.loading = false;
      },
      error: (err) => {
        console.error('‚ùå Error de login:', err);
        this.errorMessage = 'Usuario o contrase√±a inv√°lido';
        this.loading = false;
      }
    });
  }
}



