import { Component, HostListener, OnInit } from '@angular/core';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatSidenavModule } from '@angular/material/sidenav';
import { RouterModule, Router } from '@angular/router';
import { MatListModule } from '@angular/material/list';
import { CommonModule } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';
import { MatMenuModule } from '@angular/material/menu';
import { AuthService } from '../../services/auth.service'; // Aseg√∫rate de que la ruta sea correcta

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],
  standalone: true,
  imports: [MatSidenavModule, MatToolbarModule, MatListModule, RouterModule, CommonModule, MatIconModule, MatMenuModule]
})
export class DashboardComponent implements OnInit {
  roles: string[] = [];
  userName = 'Admin'; // Nombre del usuario para mostrar en la barra de herramientas
  isSidebarOpened = true;
  
  // Inyecta correctamente el AuthService
  constructor(private router: Router, private authService: AuthService) {}

  // üëâ Detectar el tama√±o de la pantalla
  @HostListener('window:resize', ['$event'])
  onResize(event: any) {
    this.isSidebarOpened = event.target.innerWidth > 600;
  }




  ngOnInit() {
    this.isSidebarOpened = window.innerWidth > 600;
    // Ahora authService est√° correctamente inyectado
    this.roles = this.authService.getRoles(); // Carga los roles del usuario logueado
  }

  // hasRole(role: string): boolean {
  //   return this.roles.some(r => r.includes(role));
  // }
  
  hasRole(role: string): boolean {
    return this.roles.includes(role);
  }

  toggleSidebar() {
    this.isSidebarOpened = !this.isSidebarOpened;
  }
   
  logout() {
    console.log('Cerrando sesi√≥n...');
    // Utiliza el m√©todo logout del AuthService en lugar de hacerlo manualmente
    this.authService.logout();
  }
}