
import { HttpInterceptorFn } from '@angular/common/http';
import { HttpRequest, HttpHandlerFn, HttpEvent, HttpErrorResponse } from '@angular/common/http';
import { Observable, catchError, throwError } from 'rxjs';
import { inject } from '@angular/core';
import { Router } from '@angular/router';

export const AuthInterceptor: HttpInterceptorFn = (
  req: HttpRequest<any>,
  next: HttpHandlerFn
): Observable<HttpEvent<any>> => {
  const router = inject(Router);

  if (req.url.includes('/api/auth/login')) {
    return next(req);
  }

  const token = localStorage.getItem('token');
  const cloned = token
    ? req.clone({ setHeaders: { Authorization: `Bearer ${token}` } })
    : req;

  return next(cloned).pipe(
    catchError((error: HttpErrorResponse) => {
      if (error.status === 401) {
        console.error('‚ö†Ô∏è Acceso no autorizado. Detalle:', error);

        // Verifica si el backend indica espec√≠ficamente "Token expirado"
        const errorMessage = error.error?.message || '';
        if (errorMessage.toLowerCase().includes('expirado')) {
          console.error('üîí Token expirado. Cerrando sesi√≥n...');
          localStorage.removeItem('token');
          router.navigate(['/']);
        } else {
          // Solo error de permisos (sin cerrar sesi√≥n)
          console.warn('‚ö†Ô∏è No tienes permisos para esta acci√≥n, pero el token sigue v√°lido.');
        }
      }
      return throwError(() => error);
    })
  );
};
