
import { Routes } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './admin/dashboard/dashboard.component';
import { AuthGuard } from './guards/auth.guard';

export const routes: Routes = [
  { path: '', component: LoginComponent },
  {
    path: 'dashboard', //  layout principal
    component: DashboardComponent,
    canActivate: [AuthGuard],
    data: { roles: ['COMISIONADO', 'ESPECIALISTA', 'EXTERNO'] },
    children: [
      {
        path: 'usuarios',
        loadComponent: () =>
          import('./admin/usuarios/usuarios.component').then(m => m.UsuariosComponent),
        data: { roles: ['COMISIONADO'] }
      },
      {
        path: 'roles',
        loadComponent: () =>
          import('./admin/roles/roles.component').then(m => m.RolesComponent),
        data: { roles: ['ESPECIALISTA'] }
      },
      {
        path: 'ficha',
        loadComponent: () =>
          import('./admin/ficha/ficha.component').then(m => m.FichaComponent),
        data: { roles: ['COMISIONADO', 'ESPECIALISTA'] }
      },
      {
        path: 'registrar-ficha',
        loadComponent: () =>
          import('./admin/ficha/registrar-ficha/registrar-ficha.component').then(m => m.RegistrarFichaComponent),
        data: { roles: ['COMISIONADO', 'ESPECIALISTA', 'EXTERNO'] }
      },
      { path: '', redirectTo: 'usuarios', pathMatch: 'full' }
    ]
  },
  {
    path: 'unauthorized',
    loadComponent: () =>
      import('./unauthorized/unauthorized.component').then(m => m.UnauthorizedComponent)
  },
  { path: '**', redirectTo: '' }
];
